import Vue from 'vue'
import Vuex from 'vuex'
import SurveyForm from '../api/SurveyForm'
Vue.use(Vuex)
export const store = new Vuex.Store({
  state: {
    ddlGender: [],
    ddlBuyProductFor: [],
    ddlPrefferedProduct: [],
    ddlCurrentUsedProduct: [],
    detailConditionSubmit: '',
    AbbottBackgroundImgUrl: '',
    AbbottSuccessImgUtl: '',
    phoneNumber: ''
  },
  mutations: {
    setDataSetup: (state, data) => {
      state.ddlGender = data.ddlGender
      state.ddlBuyProductFor = data.ddlBuyProductFor
      state.ddlPrefferedProduct = data.ddlPrefferedProduct
      state.ddlCurrentUsedProduct = data.ddlCurrentUsedProduct
      state.detailConditionSubmit = data.detailConditionSubmit.replace(/&lt;br&gt;/g, '<br>')
      // state.AbbottBackgroundImgUrl = data.AbbottBackgroundImgUrl
      // state.AbbottSuccessImgUtl = data.AbbottSuccessImgUtl
    },
    setPhoneNumber: (state, data) => {
      state.phoneNumber = data
    }
  },
  actions: {
    receiveDataSetup: ({commit}) => {
      SurveyForm.getDataSetup(result => {
        console.log('result == ', result)
        commit('setDataSetup', result)
      })
    },
    sendSmsOtp: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        SurveyForm.sendSmsOtp(result => {
          resolve(result)
        }, payload)
      })
    },
    checkDupMissionSticker: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        SurveyForm.checkDupMissionSticker(result => {
          resolve(result)
        }, payload)
      })
    },
    saveSurveyForm: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        SurveyForm.saveSurveyForm(result => {
          resolve(result)
        }, payload)
      })
    },
    sendMsg: ({ commit }, paramMsg) => {
      SurveyForm.sendMsg(result => {
        if (result) {
          console.log('sendMsg successfully', result)
        }
      }, paramMsg)
    }
  }
})



