<template>
  <div class="success-page">
    <div class="container follow-reward">
      <span>ขอบคุณที่ลงทะเบียน</span>
    </div>
    <v-footer padless justify="center" class="footer-image-thanks">    
      <div class="imgbox" @click="submit">
        <img
          class="center-fit"
          width="300px"
          src="../assets/AbbottBtnDownLoadSticker.png"
        /> 
      </div>
      <!-- <a href="https://store.line.me/stickershop/product/27813" ref="urlDownloadSticker"></a> -->
    </v-footer>
  </div>
</template>
<script>

export default {
  props: ['regisData', 'setOverlay', 'setTextError', 'setDialog', 'chanelId'],
  methods: {
    submit () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.regisData.chanelId = this.chanelId
          this.setOverlay(true)
          this.$store.dispatch('saveSurveyForm', this.regisData).then(resolve => {
            console.log('saveSurveyForm resolve', resolve)
            if (resolve.status) {
              this.setOverlay(false)
              console.log('call sendMsg')
              this.$store.dispatch('sendMsg', {
                action: 'redirectToDetailPage',
                emocApplicationId: '123456'
              })
            } else {
              this.setOverlay(false)
              this.setTextError('เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
              if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
                this.setTextError(resolve.message)
              }
              this.setDialog(true)
            }
          })
        } else {
          this.panel = 0
        }
      })
    }
  }
}
</script>
<style scoped>
  .footer-image-thanks {
    background-color: initial !important;
    /* padding-top: 30px; */
    padding-bottom: 10px;
  }
  .imgbox {
    width: 100%;
  }
  .follow-reward {
    /* border: 3px solid #1b1a75;
    background-color: white; */
    margin-top: 20%;
    border-radius: 15px;
    font-size: 45px;
    font-weight: bold;
    line-height: 2.5rem;
    color: #1f4570;
    /* letter-spacing: 1.5px; */
  }
</style>


