<template>
  <div name="register-page" class="register-page" style="background-color: white; height: auto; text-align: left;">
    <div class="container" style="color: black; text-align: center; line-height: 40px; font-size: 45px; letter-spacing: 1.5px;">
      <span>ชวนคุณลงทะเบียน</span><br>
      <span style="font-weight:bold;">เพื่อรับฟรีสติกเกอร์</span><br>
      <span style="font-weight:bold; color: #0281b6;">Abbott Cares</span><br>
    </div>
    <div class="container" style="color: black;">
      <div class="row no-gutters">
        <div class="col-5">
          <label> ชื่อ (Name)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.Name__c"
            v-validate="'required'"
            data-vv-as="ชื่อ (Name)"
            :error="errors.has('Name__c')"
            name="Name__c"
          >
            <template #error>
              <span>{{ errors.first('Name__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-5">
          <label>นามสกุล (Surname)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.Surname__c"
            v-validate="'required'"
            data-vv-as="นามสกุล (Surname)"
            :error="errors.has('Surname__c')"
            name="Surname__c"
            >
            <template #error>
              <span>{{ errors.first('Surname__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>
      
      <div class="row no-gutters">
        <div class="col-5">
          <label>อีเมล (Email)</label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.Email__c"
            v-validate="'email'"
            data-vv-as="อีเมล (Email)"
            :error="errors.has('Email__c')"
            name="Email__c"
            >
            <template #error>
              <span>{{ errors.first('Email__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>

      <div class="row no-gutters">
        <div class="col-5">
          <label>เบอร์โทร (Phone No.)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.PhoneNo__c"
            v-validate="'required|phone'"
            data-vv-as="เบอร์โทร (Phone Number)"
            :error="errors.has('PhoneNo__c')"
            name="PhoneNo__c"
            :maxlength="10"
            :inputType="'number'"
          >
            <template #error>
              <span>{{ errors.first('PhoneNo__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>

      <div class="row no-gutters">
        <div class="col-12">
          <label>คุณคิดว่าผลิตภัณฑ์อาหารเสริมผู้ใหญ่เหมาะกับใครที่สุด<abbr class="slds-required" title="required">* </abbr></label>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <combobox-std
            v-model="regisData.Buy_Product_For__c"
            v-validate="'required'"
            data-vv-as="เหมาะกับใคร"
            data-vv-value-path="textInputValue"
            name="Buy_Product_For__c"
            :error="errors.has('Buy_Product_For__c')"
            :options="buyProductForList"
            label=""
          >
            <template #error>
              <span>{{ errors.first('Buy_Product_For__c') }}</span>
            </template>
          </combobox-std>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <label>กรุณาเลือกผลิตภัณฑ์ของแอ็บบอทที่คุณสนใจมากที่สุด<abbr class="slds-required" title="required">* </abbr></label>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <combobox-std
            v-model="regisData.Preferred_Product__c"
            v-validate="'required'"
            data-vv-as="สนใจมากที่สุด"
            data-vv-value-path="textInputValue"
            name="Preferred_Product__c"
            :error="errors.has('Preferred_Product__c')"
            :options="preferredProductList"
            label=""
          >
            <template #error>
              <span>{{ errors.first('Preferred_Product__c') }}</span>
            </template>
          </combobox-std>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <label>ผลิตภัณฑ์ใดที่คุณใช้ในปัจจุบัน<abbr class="slds-required" title="required">* </abbr></label>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <combobox-std
            v-model="regisData.Current_Used_Product__c"
            v-validate="'required'"
            data-vv-as="ใช้ในปัจจุบัน"
            data-vv-value-path="textInputValue"
            name="Current_Used_Product__c"
            :error="errors.has('Current_Used_Product__c')"
            :options="currentUsedProductList"
            label=""
          >
            <template #error>
              <span>{{ errors.first('Current_Used_Product__c') }}</span>
            </template>
          </combobox-std>
        </div>
      </div>
    </div>
    <div class="container text-condition-submit">
      <div class="row">
        <div class="col-1">
          <checkbox-std
            v-model="checkConfirmCondition"
            :error="!checkConfirmCondition"
          >
          </checkbox-std>
        </div>
        <div class="col-11">
          <span v-html="detailConditionSubmit !== '' ? detailConditionSubmit : conditionDefault"></span>
        </div>
      </div>
      <div class="row">
        <div class="col-12" style="padding-top:0px; z-index:1;">
          <v-btn
            @click="submit()"
            class="thaiSansExtraBold"
            rounded color="primary" dark
            :elevation="2"
            :disabled="!checkConfirmCondition"
          >
            ส่งคำตอบ
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['value', 'setNumPage', 'setNumPage', 'setOverlay', 'setOtpNumber', 'setTextError', 'setDialog', 'mid'],
  data () {
    return {
      panel: null,
      regisData: {
        // Name__c: '123',
        // Surname__c: '123',
        // Email__c: '123@123.ccc',
        // PhoneNo__c: '1234567890',
        // Buy_Product_For__c: 'ตัวเอง',
        // Preferred_Product__c: 'Ensure',
        // Current_Used_Product__c: 'Ensure',
        // MID__c: 'xxx',
        // CR__c: 'Line'
        Name__c: '',
        Surname__c: '',
        Email__c: '',
        PhoneNo__c: '',
        Buy_Product_For__c: '',
        Preferred_Product__c: '',
        Current_Used_Product__c: '',
        MID__c: '',
        CR__c: ''
      },
      conditionDefault: 'ผู้เข้าร่วมกิจกรรมยินดีให้บริษัท แอ๊บบอต ลาบอแรตอริส จำกัด' +
      '("บริษัทฯ“) ติดต่อทางโทรศัพท์ ข้อความ อีเมล และไปรษณีย์ เพื่อให้' +
      'คำปรึกษาด้านโภชนาการ คำแนะนำเกี่ยวกับผลิตภัณฑ์ โปรโมชั่นพิเศษต่างๆ โดยบริษัทฯ จะเก็บรักษาข้อมูลของผู้เข้าร่วมกิจกรรมไว้เป็นความลับและไม่เผยแพร่ต่อบุคคลอื่น<br><br>' +
      'หมายเหตุ: ท่านสามารถแจ้งยกเลิกการติดต่อจากทางบริษัทฯ ได้' +
      'ตลอดเวลา โดยโทรผ่านทาง Abbott Cares 0-2252-2448 ในเวลาทำการ (08:30 น.- 17:30 น.) จันทร์-ศุกร์ เว้นวันหยุดราชการ',
      checkConfirmCondition: false
    }
  },
  mounted () {
    this.panel = 0
    this.$nextTick(() => {
      this.panel = null
    })
  },
  computed: {
    // phoneNumber () { return this.$store.state.phoneNumber },
    genderList () { return this.$store.state.ddlGender },
    buyProductForList () { return this.$store.state.ddlBuyProductFor },
    preferredProductList () { return this.$store.state.ddlPrefferedProduct },
    currentUsedProductList () { return this.$store.state.ddlCurrentUsedProduct },
    detailConditionSubmit () { return this.$store.state.detailConditionSubmit }
  },
  methods: {
    submit () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.regisData.MID__c = this.mid
          let paramSendSms = {
            phoneNumber: this.regisData.PhoneNo__c,
            mid: this.mid
          }
          this.setOverlay(true)
          this.$store.dispatch('checkDupMissionSticker', paramSendSms).then(resolve => {
            if (resolve.status) {
              this.$emit('input', this.regisData)
              this.$store.commit('setPhoneNumber', this.regisData.PhoneNo__c)
              this.setOverlay(false)
              this.setNumPage(2)
            } else {
              this.setOverlay(false)
              this.setTextError('เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
              if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
                this.setTextError(resolve.message)
              }
              this.setDialog(true)
            }
          })
        } else {
          this.panel = 0
        }
      })
    }
  }
}
</script>

<style scoped>
  label {
    color: black;
  }
  /deep/ .slds-input {
    line-height: 1.5rem;
    min-height: 1.5rem;
  }
  /deep/ .v-item-group {
    position: unset;
  }
  /deep/ .v-expansion-panel-header {
    padding: 0 12px;
    font-size: .8125rem;
    min-height: 20px;
    width: auto;
  }
  /deep/ .v-expansion-panel-header__icon {
    border: solid 1px #e0e0e0;
    border-radius: 45px;
  }
  /deep/ .v-expansion-panel--active>.v-expansion-panel-header {
    min-height: 20px;
  }
  /deep/ .v-icon.v-icon {
    font-size: 15px;
  }
  /deep/ .theme--light.v-expansion-panels .v-expansion-panel-header .v-expansion-panel-header__icon .v-icon {
    color: rgb(4 129 182);
  }
  /deep/ .v-expansion-panel-content__wrap {
    padding: 0 12px;
  }
  .auto-address {
    border: 1px solid var(--slds-c-input-color-border,var(--sds-c-input-color-border,#c9c9c9));
    border-radius: var(--slds-c-input-radius-border,var(--sds-c-input-radius-border,.25rem));
    /* font-weight: bold;
    font-size: 1rem; */
    /* padding-left: var(--slds-c-input-spacing-horizontal-start,var(--sds-c-input-spacing-horizontal-start,.75rem)); */
    min-height: 1.5rem;
    margin-bottom: 2px;
  }

  .auto-address /deep/ input.vth-addr-input {
    /*font-family: 'ThaiSans Neue Extra Bold', Helvetica, Arial, sans-serif;*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding-left: 1.5rem !important;
    box-shadow: none;
    border: none !important;
  }
  .thai-address-input /deep/ input {
    width: 100%;
    outline: #8ecef0;
    padding-left: 12px !important;
    padding-right: 0.75rem;
  }
  .thai-address-input /deep/ .suggestion-list {
    color: black;
    font-size: 0.8rem;
  }
  .theme--dark.v-btn.v-btn--disabled.v-btn--has-bg {
    background-color: #f3f3f3 !important;
  }
  .theme--dark.v-btn.v-btn--disabled {
    color: darkgray !important;
  }
  .text-condition-submit {
    color: black;
    line-height: 18px;
  }
  /deep/ .theme--dark.v-btn.v-btn--has-bg {
    border: 1px solid white!important;
  }
  /deep/ .v-btn__content {
    font-size: 17px;
  }
</style>


