import LCC from 'lightning-container'
import mockup from './mock'

export default {
  getDataSetup (callback) {
    if (process.env.NODE_ENV === 'production') {
      console.log('api called')
      LCC.callApex(
        'MINTEL_MissionStickerController.getDataSetup',
        (result, event) => {
          if (event.status) {
            callback(result)
          } else if (event.type === 'exception') {
            // _statusSaveAllForRegister.status = false
            // _statusSaveAllForRegister.message = event.message
            console.log(event.message + ' : ' + event.where)
            callback(event)
          } else {
            console.log('Unknown Error', event)
          }
        },
        {escape: false}
      )
    } else {
      setTimeout(() => callback(mockup.getDataSetup()), 100)
    }
  },
  sendSmsOtp (callback, payload) {
    if (process.env.NODE_ENV === 'production') {
      console.log('api called')
      LCC.callApex(
        'MINTEL_MissionStickerController.sendSmsOtp', payload,
        (result, event) => {
          if (event.status) {
            callback(result)
          } else if (event.type === 'exception') {
            // _statusSaveAllForRegister.status = false
            // _statusSaveAllForRegister.message = event.message
            console.log(event.message + ' : ' + event.where)
            callback(event)
          } else {
            console.log('Unknown Error', event)
          }
        },
        {escape: false}
      )
    } else {
      setTimeout(() => callback(mockup.sendSmsOtp()), 100)
    }
  },
  checkDupMissionSticker (callback, payload) {
    if (process.env.NODE_ENV === 'production') {
      console.log('api called')
      LCC.callApex(
        'MINTEL_MissionStickerController.checkDupMissionSticker', payload,
        (result, event) => {
          if (event.status) {
            callback(result)
          } else if (event.type === 'exception') {
            // _statusSaveAllForRegister.status = false
            // _statusSaveAllForRegister.message = event.message
            console.log(event.message + ' : ' + event.where)
            callback(event)
          } else {
            console.log('Unknown Error', event)
          }
        },
        {escape: false}
      )
    } else {
      setTimeout(() => callback(mockup.checkDupMissionSticker()), 100)
    }
  },
  saveSurveyForm (callback, payload) {
    if (process.env.NODE_ENV === 'production') {
      console.log('payload ==', payload)
      LCC.callApex(
        'MINTEL_MissionStickerController.saveSurveyForm', payload,
        (result, event) => {
          if (event.status) {
            callback(result)
          } else if (event.type === 'exception') {
            // _statusSaveAllForSignIn.status = false
            // _statusSaveAllForSignIn.message = event.message
            console.log(event.message + ' : ' + event.where)
            callback(event)
          } else {
            console.log('Unknown Error', event)
          }
        },
        {escape: false}
      )
    } else {
      setTimeout(() => callback(mockup.sendSmsOtp()), 100)
    }
  },
  sendMsg (callback, paramMsg) {
    if (process.env.NODE_ENV === 'production') {
      LCC.sendMessage(paramMsg)
      console.log('sendMsgApi')
      callback()
    }
  }
}



