<template>
  <div name="otp-verification-page" class="otp-verification-page">
    <v-container>
      <v-row no-gutters>
        <v-col cols="12" class="mt-2">
          <i class="fa fa-chevron-left" aria-hidden="true" style="color: #009ce5;" @click="backTo"></i>
          <span class="font-weight thaiSansExtraBold">กรอกรหัส OTP ที่คุณได้รับทาง SMS</span> 
        </v-col>
      </v-row>
      <v-row no-gutters class="inputData">         
        <v-col cols="12">
          <div class="ma-auto" style="max-width: 200px">
            <v-form ref="form">
              <v-otp-input
                v-model="otpInput"
                ref="otpInput"
                length="4"
                type="number"
                inputmode="decimal"
                @finish="nextTo">
              </v-otp-input>
            </v-form>
          </div>
        </v-col>
      </v-row>  
      <v-row no-gutters>
        <v-col cols="12">
          <div class="titleText">
            <div class="alert-time-otp">
              <span>กรุณาใส่รหัสผ่านภายใน {{timerCount - ((currentTime - datetimeOtpRequested) / 1000) | minutesAndSeconds}} </span>
            </div>
          </div>
        </v-col>             
      </v-row>
      <v-btn
        @click="resendOTP"
        class="borderWh thaiSansExtraBold"
        color="primary"
        rounded
        dark
      >
        ส่งอีกครั้ง
      </v-btn>
    </v-container>
  </div>
</template>

<script>
export default {
  props: ['setNumPage', 'otpNumber', 'setOverlay', 'setOtpNumber', 'setTextError', 'setDialog', 'mid'],
  computed: {
    phoneNumber () { return this.$store.state.phoneNumber }
  },
  data () {
    return {
      otpInput: '',
      timerCount: 60 * 2,
      datetimeOtpRequested: null,
      currentTime: null,
      timerInterval: 133,
      overlay: false,
      textError: '',
      dialog: false
    }
  },
  created () {
    if (this.timerCount > 0) {
      this.timer = setInterval(() => {
        this.currentTime = Date.now()
        if (this.timerCount - ((this.currentTime - this.datetimeOtpRequested) / 1000) <= 0) {
          this.timeout = true
          clearInterval(this.timer)
        }
      }, this.timerInterval)
    }
    this.datetimeOtpRequested = Date.now()
    this.currentTime = Date.now()
  },
  filters: {
    minutesAndSeconds (value) {
      var minutes = Math.floor(parseInt(value, 10) / 60)
      var seconds = parseInt(value, 10) - minutes * 60
      seconds = seconds.toString().padStart(2, '0')
      if (value <= 0) {
        return `0:00`
      }
      return `${minutes}:${seconds}`
    }
  },
  methods: {
    backTo () {
      this.timerCount = 60 * 2
      this.timeout = true
      clearInterval(this.timer)
      this.otpInput = ''
      this.$refs.otpInput.otp = []
      this.setNumPage(1)
    },
    nextTo () {
      if (this.timeout) {
        this.setOverlay(false)
        this.setTextError('OTP Time-out')
        this.setDialog(true)
      } else {
        if (this.otpNumber === this.otpInput) {
          this.setNumPage(4)
        } else {
          this.setTextError('Wrong OTP code')
          this.setDialog(true)
        }
      }
    },
    resendOTP () {
      this.$refs.otpInput.otp = []
      this.otpInput = []
      this.timerCount = 60 * 2
      clearInterval(this.timer)
      this.setOverlay(true)
      let paramSendSms = {
        phoneNumber: this.phoneNumber,
        mid: this.mid
      }
      this.$store.dispatch('sendSmsOtp', paramSendSms).then(resolve => {
        if (resolve.status) {
          this.setOverlay(false)
          this.setNumPage(3)
          this.setOtpNumber(resolve.otpNumber)
        } else {
          this.setOverlay(false)
          this.setTextError('เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
          if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
            this.setTextError(resolve.message)
          }
          this.setDialog(true)
        }
      })
      this.timeout = false
      this.timer = setInterval(() => {
        this.currentTime = Date.now()
        if (this.timerCount - ((this.currentTime - this.datetimeOtpRequested) / 1000) <= 0) {
          this.timeout = true
          clearInterval(this.timer)
        }
      }, this.timerInterval)
      this.datetimeOtpRequested = Date.now()
      this.currentTime = Date.now()
    }
  }
}
</script>

<style scoped>
/deep/ .v-text-field--outlined>.v-input__control>.v-input__slot {
  min-height: 45px;
  font-size: 20px;
}
/deep/ .v-text-field__slot,.v-input__slot {
    border: 1px solid white;
    border-radius: 5px;
    border-color: rgb(142 206 240);
}
.font-weight {
  font-size: 22px;
  font-weight: bold;
  color: #0e4681;
}
.alert-time-otp {
  font-size: 14.05pt;
  font-weight: bold;
  color: red;
}
.inputData {
  margin-top: 40px!important;
}
.borderWh {
  border: 1px solid white!important;
}
.v-btn {
  font-size: 17px;
}
/deep/ .v-label {
  font-size: 20px;
}
</style>



