<template>
  <div name="index" class="bodyAllpage" :class="{'background-success': numpage === 4}">
  <!-- :style="{'background-image': 'url(' + require('../assets/AbbottBackground.png') + ')'}" -->
  <!-- style="background-image: url('AbbottBackground.png')" -->
    <v-overlay :value="overlay">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <v-footer v-show="numpage !== 1" padless justify="center" style="background-color: initial;">    
      <div class="imgbox">
        <img width="40px" src="../assets/AbbottHeader.png"/> 
      </div>
    </v-footer>
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title class=" justify-end">
          <v-btn
            class=" justify-end"
            color="primary"
            text
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text class="text-red">
          {{textError}}
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <div class="bodyInfo">
      <div v-show="numpage === 0 && textError !== ''"
           class="aleady-mission">{{textError}}
      </div>
      <register-page v-if="numpage === 1"
                v-model="regisData"
                :setNumPage="setNumPage"
                :setOverlay="setOverlay"
                :setOtpNumber="setOtpNumber"
                :setTextError="setTextError"
                :setDialog="setDialog"
                :mid="mid"
      />
      <send-sms v-if="numpage === 2"
                :setNumPage="setNumPage"
                :setOverlay="setOverlay"
                :setOtpNumber="setOtpNumber"
                :setTextError="setTextError"
                :setDialog="setDialog"
                :mid="mid"
      />
      <otp-verification-page v-if="numpage === 3"
                :otpNumber="otpNumber"
                :setNumPage="setNumPage"
                :setOverlay="setOverlay"
                :setOtpNumber="setOtpNumber"
                :setTextError="setTextError"
                :setDialog="setDialog"
                :mid="mid"
      />
      <success-page v-if="numpage === 4"
                :regisData="regisData"
                :setOverlay="setOverlay"
                :setTextError="setTextError"
                :setDialog="setDialog"
                :chanelId="chanelId"
      />
    </div>
    <div color="#FFF" class="footer">
      TH.2022.30533.ENS-GLU-PDS.1 (v1.0) &#169;2022Abbott
      <!-- <div class="imgbox">
        <img class="center-fit" src="../assets/AbbottFooter.png"/> 
      </div> -->
    </div>
  </div>
</template>

<script>
import otpVerificationPage from './OTPVerificationPage.vue'
import RegisterPage from './RegisterPage.vue'
import SendSms from './SendSMS.vue'
import SuccessPage from './SuccessPage.vue'

export default {
  data () {
    return {
      overlay: false,
      textError: '',
      dialog: false,
      numpage: 0,
      otpNumber: '',
      mid: '',
      chanelId: '',
      regisData: {
        Name__c: '',
        Surname__c: '',
        Email__c: '',
        PhoneNo__c: '',
        Buy_Product_For__c: '',
        Preferred_Product__c: '',
        Current_Used_Product__c: '',
        MID__c: '',
        CR__c: '',
        chanelId: ''
      }
    }
  },
  created () {
    this.$store.dispatch('receiveDataSetup')
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.mid = urlParams.get('mid')
    this.chanelId = urlParams.get('chanelId')
    console.log('mid', this.mid)
    console.log('chanelId', this.chanelId)
    let paramSendSms = {
      phoneNumber: 'DummyNumber',
      mid: this.mid
    }
    this.$store.dispatch('checkDupMissionSticker', paramSendSms).then(resolve => {
      if (resolve.status) {
        this.setNumPage(1)
        this.setOtpNumber(resolve.otpNumber)
      } else {
        this.setOverlay(false)
        // this.setTextError('ไม่พบเบอร์โทร/เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
        if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
          this.setTextError(resolve.message)
        }
      }
    })
  },
  methods: {
    onlyNumber ($event) {
      let keyCode = ($event.keyCode ? $event.keyCode : $event.which)
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        $event.preventDefault()
      }
    },
    setNumPage (numpage) {
      this.numpage = numpage
    },
    setOverlay (flg) {
      this.overlay = flg
    },
    setOtpNumber (otpNumber) {
      this.otpNumber = otpNumber
    },
    setTextError (textError) {
      this.textError = textError
    },
    setDialog (dialog) {
      this.dialog = dialog
    }
  },
  components: {
    otpVerificationPage,
    RegisterPage,
    SendSms,
    SuccessPage
  }
}
</script>

<style scoped>
  .bodyAllpage {
    min-height: 100vh;
    background-image: url('../assets/AbbottBackground.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    font-size: 17px;
  }
  .background-success {
    background-image: url('../assets/AbbottSuccess.png') !important;
  }
  /* @media only screen and (orientation: landscape) {
    .bodyAllpage {
      height: 250vh;
      min-height: none;
    }
  } */
  .imgbox {
    display: grid;
    height: 100%;
  }
  .center-fit {
    max-width: 100%;
    max-height: 100vh;
    margin: auto;
  }
  .bodyInfo{
    margin: 0px 13px;
    color:#8ECEF1;
    height: auto;
  }
  .imgLogo {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .v-btn {
    width: 50%;
    height: 30px!important;
    font-size: 11.37pt;
  }
  .footer {
    text-align: right;
    color: white;
    bottom: 0;
    width: 100%;
    /* justify-content: right; */
    position: fixed;
    /* z-index: 1; */
    padding-right: 5px;
    font-size: 17px;
  }

  .v-card  {
    text-align: center;
    border: 3px solid #8ECEF1;
  }

  .text-blue {
    color: #64C8F5!important;
  }

  .text-red {
    color: red!important;
  }
  .aleady-mission {
    color:red;font-size: 24px;
    background-color:white;
    margin-top: 50px;
    border: 1px solid #1b1a75;
    border-radius: 5px;
  }
  /deep/ .v-card__text {
    font-size: 24px;
  }
</style>



