<template>
  <div class="send-sms">
      <v-container>
        <v-row no-gutters>
          <v-col cols="12" class="mt-2">
            <i class="fa fa-chevron-left" aria-hidden="true" style="color: #009ce5;" @click="backTo"></i>
            <span class="font-weight thaiSansExtraBold"> กรุณากรอกเบอร์โทรศัพท์ของคุณ </span>
          </v-col>
        </v-row>
        <v-form ref="form" >
          <v-row no-gutters class="inputData">
            <v-col cols="12">
              <v-text-field class="required thaiSansExtraBold"
                            v-model="phoneNumber"
                            :rules="PhonenumberRules"
                            @keypress="onlyNumber"
                            label="หมายเลขโทรศัพท์"
                            filled rounded clearable dense single-line
                            maxlength="10"
                            inputmode="decimal"
                            disabled
              ></v-text-field>
            </v-col>
          </v-row>
        </v-form>
        <v-btn
          @click="nextTo()"
          class="thaiSansExtraBold"
          rounded
          color="primary"
          dark
          :elevation="2"
        >
          ส่ง SMS รับรหัส OTP
        </v-btn>
      </v-container>
  </div>
</template>

<style scoped>
.inputData {
  margin-top: 40px!important;
}

.font-weight {
  font-size: 22.5px;
  font-weight: bold;
  color: #0e4681;
}

/deep/ .theme--dark.v-btn.v-btn--has-bg {
  border: 1px solid white!important;
}
.text-blue {
  color: #64C8F5!important;
}

.text-red {
  color: red!important;
}

/deep/ .theme--light.v-text-field--filled>.v-input__control>.v-input__slot {
  background-color: white;
  border: 2px solid #8ecef0;
  font-weight: bold;
  font-size: 20px;
}

/deep/ .v-text-field.v-text-field--enclosed .v-text-field__details {
  margin-bottom: 0px;
}

/deep/ .v-card.v-sheet.theme--light {
    border-radius: 30px;
}

/deep/ .v-dialog.v-dialog--active {
    border-radius: 30px;
}
/deep/ .v-btn__content {
  font-size: 17px;
}
/deep/ .v-label {
  font-size: 20px;
}
</style>


<script>

export default {
  props: ['setNumPage', 'setOverlay', 'setOtpNumber', 'setTextError', 'setDialog', 'mid'],
  data () {
    return {
      // phoneNumber: '',
      PhonenumberRules: [
        v => !!v || 'กรุณาระบุเบอร์โทร',
        v => (v && v.length === 10) || 'กรุณาตรวจสอบความถูกต้องของหมายเลขโทรศัพท์ในฟิลด์ Mobile  ตัวอย่างเช่น 09XXXXXXXX (10 หลัก)'
      ]
    }
  },
  computed: {
    phoneNumber () { return this.$store.state.phoneNumber }
  },
  methods: {
    onlyNumber ($event) {
      let keyCode = ($event.keyCode ? $event.keyCode : $event.which)
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) { // 46 is dot
        $event.preventDefault()
      }
    },
    backTo () {
      this.setNumPage(1)
    },
    nextTo () {
      if (this.$refs.form.validate()) {
        this.setOverlay(true)
        let paramSendSms = {
          phoneNumber: this.phoneNumber,
          mid: this.mid
        }
        this.$store.dispatch('sendSmsOtp', paramSendSms).then(resolve => {
          if (resolve.status) {
            this.setOverlay(false)
            this.setNumPage(3)
            this.setOtpNumber(resolve.otpNumber)
          } else {
            this.setOverlay(false)
            this.setTextError('ไม่พบเบอร์โทร/เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
            if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
              this.setTextError(resolve.message)
            }
            this.setDialog(true)
          }
        })
      }
    }
  }
}
</script>


